//  token
const whiteList = [
	'/pages/index/index',
	'/pages/personal/personal',
	'/pages/login/login',
	"/pages/serveTrem/serveTrem"
];
// 
const loginPage = '/pages/login/login';
export default function initPermission() {
	/**
	 * 
	 */
	const list = ['navigateTo', 'redirectTo', 'reLaunch', 'switchTab'];
	list.forEach((item) => { // ,uni.navigateTo,uni.redirectTo,uni.reLaunch,uni.switchTab4
		uni.addInterceptor(item, {
			invoke(e) { // 
				console.log('', e);
				// token
				const token = uni.getStorageSync('token');
				// url"?""?"
				const url = e.url.split('?')[0];
				const notNeed = whiteList.includes(url);
				// whiteList
				if (notNeed) {
					return e;
				} else {
					// 
					if (!token) {
						uni.showToast({
							title: '',
							icon: 'none',
						});
						uni.reLaunch({
							url: loginPage,
						});
						return false;
					} else {
						return e;
					}
				}
			},
			fail(err) { // 
				console.log(err);
			},
		});
	});
}
