// 

export default (url, method, params) => {
	let token = uni.getStorageSync('token');
	// 
	uni.showLoading({
		title: ""
	});

	return new Promise((resolve, reject) => {
		uni.request({
				url: "https://www.ydtloan.com" + url,
				method: method,
				header: {
					token:token,
					"Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
				},
				data: {
					...params
				},
				success(res) {
					if(res.statusCode === 200) {
						// 
						resolve(res.data)
					} else if(res.statusCode != 200) {
						const err = res;
						console.log('')
						if (err && err.statusCode) {
							switch (err.statusCode) {
								case 301:
									err.message = ''
									break
								case 302:
									err.message = ' URI'
									break
								case 304:
									err.message = ''
									break
								case 305:
									err.message = ''
									break
								case 400:
									err.message = ''
									break
								case 402:
									err.message = ''
									break
								case 403:
									err.message = ','
									break
								case 404:
									err.message = ''
									break
								case 407:
									err.message = ''
									break
								case 415:
									err.message = ''
									break
								case 417:
									err.message = ''
									break
								case 426:
									err.message = ''
									break
								case 429:
									err.message = ''
									break
								case 500:
									err.message = ''
									break
								case 501:
									err.message = ''
									break
								case 502:
									err.message = ''
									break
								case 503:
									err.message = ''
									break
								case 504:
									err.message = ''
									break
								case 999:
									err.message = ''
									break
							}
							reject(err);
						}
					}
				},
				fail(err) {
					err.message = '!'
					reject(err)
				},
				complete() {
					// 
					uni.hideLoading();
				}
		});
	});
};